$mozBack: -moz-radial-gradient(0 100%, circle, rgba(204,0,0,0) 14px, #f1ac11 15px),
          -moz-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 14px, #f1ac11 15px),
          -moz-radial-gradient(100% 0, circle, rgba(204,0,0,0) 14px, #f1ac11 15px),
          -moz-radial-gradient(0 0, circle, rgba(204,0,0,0) 14px, #f1ac11 15px);
$oBack: -o-radial-gradient(0 100%, circle, rgba(204,0,0,0) 14px, #f1ac11 15px),
        -o-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 14px, #f1ac11 15px),
        -o-radial-gradient(100% 0, circle, rgba(204,0,0,0) 14px, #f1ac11 15px),
        -o-radial-gradient(0 0, circle, rgba(204,0,0,0) 14px, #f1ac11 15px);
$wBack: -webkit-radial-gradient(0 100%, circle, rgba(204,0,0,0) 14px, #f1ac11 15px),
        -webkit-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 14px, #f1ac11 15px),
        -webkit-radial-gradient(100% 0, circle, rgba(204,0,0,0) 14px, #f1ac11 15px),
        -webkit-radial-gradient(0 0, circle, rgba(204,0,0,0) 14px, #f1ac11 15px);

$mozBackCart: -moz-radial-gradient(0 100%, circle, rgba(204,0,0,0) 14px, #fff 15px),
              -moz-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 14px, #fff 15px),
              -moz-radial-gradient(100% 0, circle, rgba(204,0,0,0) 14px, #fff 15px),
              -moz-radial-gradient(0 0, circle, rgba(204,0,0,0) 14px, #fff 15px);
$oBackCart: -o-radial-gradient(0 100%, circle, rgba(204,0,0,0) 14px, #fff 15px),
            -o-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 14px, #fff 15px),
            -o-radial-gradient(100% 0, circle, rgba(204,0,0,0) 14px, #fff 15px),
            -o-radial-gradient(0 0, circle, rgba(204,0,0,0) 14px, #fff 15px);
$wBackCart: -webkit-radial-gradient(0 100%, circle, rgba(204,0,0,0) 14px, #fff 15px),
            -webkit-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 14px, #fff 15px),
            -webkit-radial-gradient(100% 0, circle, rgba(204,0,0,0) 14px, #fff 15px),
            -webkit-radial-gradient(0 0, circle, rgba(204,0,0,0) 14px, #fff 15px);
//VARIABLES ^^^


header {
  width: 100%;
  height: 100vh;
  min-height: 760px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: grey;
  overflow: hidden;

  .logo-burger {
    width: 100%;
    height: 200px;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
      width: 270px;
      height: 60px;
      margin-left: 70px;
      font-size: 1.2rem;
      padding-right: 40px;
      position: relative;
      z-index: 444;
      &::before {
        content: "";
        width: 2px;
        height: 100%;
        background-color: #f1ac11;
        position: absolute;
        left: 50%;
        top: 0;
        margin-left: -1px;
      }
      p {
        line-height: 60px;
        text-align: right;
      }
      span.logo-cup {
        display: block;
        width: 25px;
        height: 20px;
        position: absolute;
        left: 40px;
        top: 30px;
        margin-top: -10px;
        border: 3px solid #000;
        border-bottom: none;
        border-radius: 50% 50% 0 0;
        &::before {
          content: "";
          position: absolute;
          top: -9px;
          left: 50%;
          margin-left: -3px;
          width: 0;
          height: 0;
          border: 3px solid #000;
          border-radius: 50%;
        }
        &::after {
          content: "";
          position: absolute;
          left: -6px;
          bottom: 0;
          width: 31px;
          height: 3px;
          background-color: #000;

        }
      }
      background: #f1ac11;
      background: $mozBack;
      background: $oBack;
      background: $wBack;
      background-position: bottom left, bottom right, top right, top left;
      -moz-background-size: 50% 50%;
      -webkit-background-size: 50% 50%;
      background-size: 50% 50%;
      background-repeat: no-repeat;
    }
    .burger-wrapper {
      position: relative;
      right: 20px;
      bottom: 80px;
      width: 30px;
      height: 30px;
      z-index: 444;
      visibility: hidden;

      &:hover {
        cursor: pointer;
      }

      .burger {
        display: block;
        width: 30px;
        height: 3px;
        background-color: #f1ac11;
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -1.5px;
        transition: 0.1s;
        &::before {
          content: "";
          width: 30px;
          height: 3px;
          position: absolute;
          left: 0;
          top: -7px;
          background-color: #f1ac11;
          transition: 0.2s;
        }
        &::after {
          content: "";
          width: 30px;
          height: 3px;
          position: absolute;
          left: 0;
          bottom: -7px;
          background-color: #f1ac11;
          transition: 0.2s;
        }
      }
    }

  }
  nav {
    width: 100%;
    height: 200px;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    ul {
      padding-left: 170px;
      li {
        margin-right: 30px;
        display: inline;
        position: relative;
        z-index: 444;
        a {
          font-size: 1.4rem;
          font-weight: bold;
          font-style: italic;
          color: #fff;
          text-transform: uppercase;
          letter-spacing: 1.1px;
          transition: 0.2s;
          &:hover {
            color: #f1ac11;
            &::after {
              width: 100%;
            }
          }
          &::after {
            content: "";
            position: absolute;
            width: 0;
            height: 3px;
            background-color: #f1ac11;
            left: 0;
            bottom: -10px;
            transition: 0.3s;
          }

        }
      }
    }
  }

  .meat {
    width: 50%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 50px;
    background-image: url("../images/meatBack.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 222;
    &::before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(1, 1, 1, 0.8);
      transition: 0.3s;
    }
    &:hover {
      .cart {
        opacity: 1;
        margin-top: -20px;
      }
      &::before {
        background-color: rgba(1, 1, 1, 0.5);
      }
    }
  }
  .vegan {
    width: 40%;
    height: 140%;
    position: absolute;
    top: -100px;
    left: 50%;
    margin-left: -20%;
    border-left: 3px solid #f1ac11;
    border-right: 3px solid #f1ac11;
    z-index: 333;
    transform: rotate(10deg);
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url("../images/veganBack.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    &::before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(1, 1, 1, 0.5);
      transition: 0.3s;
    }
    .cart {
      transform: rotate(-10deg);
      opacity: 1;
    }

  }
  .secrets {
    width: 50%;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    background: #grey;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 50px;
    background-image: url("../images/secretsBack.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    &::before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(1, 1, 1, 0.8);
      transition: 0.3s;
    }
    &:hover {
      .cart {
        opacity: 1;
        margin-top: -20px;
      }
      &::before {
        background-color: rgba(1, 1, 1, 0.5);
      }
    }
  }
  .meat>.cart, .vegan>.cart, .secrets>.cart {
    width: 370px;
    height: 205px;
    padding: 0 60px 0 60px;
    background: #fff;
    opacity: 0.4;
    transition: 0.3s;
    background: $mozBackCart;
    background: $oBackCart;
    background: $wBackCart;
    background-position: bottom left, bottom right, top right, top left;
    -moz-background-size: 50% 50%;
    -webkit-background-size: 50% 50%;
    background-size: 50% 50%;
    background-repeat: no-repeat;
    position: relative;

    &::before {
      content: "";
      width: 100%;
      height: 10px;
      position: absolute;
      background-color: #fff;
      left: 0;
      top: 50%;
      margin-top: -5px;
    }
    &::after {
      content: "";
      width: 10px;
      height: 100%;
      position: absolute;
      background-color: #fff;
      left: 50%;
      top: 0;
      margin-left: -5px;
      z-index: -1;
    }
    h3 {
      font-size: 2.2rem;
      color: #000;
      font-weight: bold;
      margin-top: 44px;
      text-align: center;
      text-transform: uppercase;
    }
    button {
      text-transform: uppercase;
      font-size: 0.9rem;
      display: block;
      width: 140px;
      border: none;
      background-color: #f1ac11;
      text-align: center;
      padding: 10px 0 10px 0;
      margin: 0 auto;
      margin-top: 20px;
      font-weight: bold;
      &:hover {
        cursor: pointer;

      }
    }
  }
  .vegan>.cart {
    bottom: 90px;
    position: relative;
    opacity: 1;
  }
}

@media screen and (max-width: 1500px) {
  header {
    nav {
      ul {
        li {
          a {
            font-size: 1rem;
          }
        }
      }
    }
    .meat>.cart, .vegan>.cart, .secrets>.cart {
      width: 270px;
      h3 {
        font-size: 1.5rem;
      }
      button {

      }
    }
  }
}
@media screen and (max-width: 1300px) {
  header {
    nav {
      height: 100vh;
      background-color: rgba(1, 1, 1, 0.9);
      z-index: 333;
      top: -100%;
      transition: 0.3s;
      ul {
        padding: 0;
        li {
          display: block;
          margin-right: 0;
          margin-bottom: 50px;
          a {
            font-size: 1.7rem;
            text-align: center;
          }
        }
      }
    }
    .nav-active {
      top: 0;
    }
    .logo-burger {
      .logo {
        bottom: 30px;
        opacity: 0;
        transition: 0.4s;
      }
      .logo-active {
        opacity: 1;
      }
      .burger-wrapper {
        visibility: visible;
        .burger-active {
          height: 0;
          &::after {
            bottom: -1.5px;
            transform: rotate(-45deg);
          }
          &::before {
            top: -1.5px;
            transform: rotate(45deg);
          }
        }
      }

    }
    .meat, .vegan, .secrets {
      position: relative;
      width: 100%;
      height: 50%;
      justify-content: center;
      align-items: center;
      padding: 0;
      z-index: 111;
      &::before {
        background-color: rgba(1, 1, 1, 0.5);
      }
      .cart {
        opacity: 1;
      }
    }
    .vegan {
      transform: rotate(0);
      top: 0;
      left: 0;
      margin-left: 0;
      border-left: none;
      border-right: none;
      .cart {
        transform: rotate(0);
        bottom: 0;
      }
    }
  }
}
@media screen and (max-width: 400px) {
  header {
    .logo-burger {
      .logo {
        margin-left: 20px;
        font-size: 1rem;
      }
    }
  }
}
